<template>
  <div class="evalue-oragn-set">
    <div class="topTitle">
      <span class="topTitle-span1">模型管理</span>
      <span class="topTitle-span1">
        <img src="../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">风险评估机构设置</span>
      <span class="topTitle-span1">
        <img src="../../assets/topTitle1.png" alt />
      </span>
    </div>
    <div class="header">
        <div class="checked-content">
          <span class="title">评估机构</span>
          <span style="width:300px;height:auto;min-height:40px;position:relative;display: inline-block;">
            <treeselect
              v-model="formValidate.extraUnit"
              :multiple="true"
              :options="data1"
              placeholder
              style="position:absolute;top:15px;left:0px"/>
          </span>
          <Button type="primary">保存</Button>
        </div>
    </div>
  </div>
</template>
<script>
import Treeselect from "@riophae/vue-treeselect";
import "@riophae/vue-treeselect/dist/vue-treeselect.css";
export default {
  components: { Treeselect },
    data(){
        return{
            formValidate: {
                extraUnit: [], //提取机构
            },
            data1: [],

        }
    },
    created() {
    let that = this;
    let data = {
      applyTime: JSON.stringify(this.formValidate.applyTime), //申请时间
      extraUnit: JSON.stringify(this.formValidate.extraUnit), //提取机构
      status: JSON.stringify(this.formValidate.workFlowState) //审核状态
    };
    data = JSON.stringify(data);
    this.axios
      .post("/extract/pageReview", data, {
        headers: { "Content-Type": "application/json" }
      })
      .then(response => {
        if (response.code == "200") {
          that.cardArr2 = response.data;
        }
      });
    this.axios
      .get("/extract/getExtractDic/EMERCRGENCY_LEVEL,EXTRACT_UNIT")
      .then(function(response) {
        if (response.code == "200") {
          that.emergeLevels = response.data.EMERCRGENCY_LEVEL;
          that.extraUnits = response.data.EXTRACT_UNIT;
        }
      })
      .catch(function(error) {});
    this.axios
      .post("/system/unit/getExtractUnit")
      .then(response => {
        if (response.code == "200") {
          that.data1 = response.data;
          that.selectTreeCleanUp(that.data1);
        }
      })
      .catch(error => {});
  },
    
}
</script>
<style lang="scss" scoped>
    .evalue-oragn-set{
        .header{
            margin-left: 30px;
            .checked-content{
                .title{
                    font-size: 14px;
                    margin-right: 10px;
                }
                button{
                    margin-left: 40px;
                    vertical-align: 5px;
                }
            }
        }
    }
</style>


